@import "https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap";
@import "https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@400;500;600;700&display=swap";
.cadastro-container {
  background: #fff;
  border-radius: 10px;
  max-width: 400px;
  margin: 2rem auto;
  padding: 2rem 2rem 1.5rem;
  box-shadow: 0 2px 16px #00000012;
}

.form-group {
  margin-bottom: 1.2rem;
}

.form-label {
  color: #2d6cdf;
  margin-bottom: .3rem;
  font-weight: 500;
  display: block;
}

.form-control {
  color: #333;
  background: #f9fbfd;
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  width: 100%;
  padding: .7rem 1rem;
  font-size: 1rem;
}

.form-control:focus {
  background: #fff;
  border-color: #2d6cdf;
  outline: none;
}

.btn-primary {
  color: #fff;
  background: #2d6cdf;
  border: none;
  border-radius: 6px;
  width: 100%;
  padding: .8rem 0;
  font-size: 1.05rem;
  font-weight: 600;
  transition: background .2s;
}

.btn-primary:hover {
  background: #1b4e9b;
}

.mt-3 {
  margin-top: 1rem;
}

.mb-4 {
  margin-bottom: 1.5rem;
}

@media (width <= 600px) {
  .cadastro-container {
    max-width: 98vw;
    padding: 1.2rem .7rem 1rem;
  }
}

.display-1 {
  font-size: clamp(2.5rem, 5vw, 4rem);
  font-weight: var(--font-weight-bold);
  letter-spacing: -.02em;
  background: linear-gradient(135deg, var(--primary), var(--accent));
  -webkit-text-fill-color: transparent;
  margin-bottom: var(--space-lg);
  background-clip: text;
  line-height: 1.1;
}

.display-2 {
  font-size: clamp(2rem, 4vw, 3rem);
  font-weight: var(--font-weight-bold);
  letter-spacing: -.01em;
  color: var(--text-primary);
  margin-bottom: var(--space-md);
  line-height: 1.2;
}

.display-3 {
  font-size: clamp(1.75rem, 3vw, 2.5rem);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  margin-bottom: var(--space-md);
  line-height: 1.3;
}

.heading-1 {
  font-size: var(--font-size-4xl);
  font-weight: var(--font-weight-bold);
  color: var(--text-primary);
  margin-bottom: var(--space-lg);
  line-height: 1.2;
}

.heading-2 {
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  margin-bottom: var(--space-md);
  line-height: 1.3;
}

.heading-3 {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  margin-bottom: var(--space-md);
  line-height: 1.4;
}

.heading-4 {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-medium);
  color: var(--text-primary);
  margin-bottom: var(--space-sm);
  line-height: 1.4;
}

.heading-5 {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-medium);
  color: var(--text-primary);
  margin-bottom: var(--space-sm);
  line-height: 1.5;
}

.heading-6 {
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  margin-bottom: var(--space-sm);
  text-transform: uppercase;
  letter-spacing: .05em;
  line-height: 1.5;
}

.body-large {
  font-size: var(--font-size-lg);
  line-height: var(--line-height-relaxed);
  color: var(--text-secondary);
  margin-bottom: var(--space-md);
}

.body-medium {
  font-size: var(--font-size-base);
  line-height: var(--line-height-normal);
  color: var(--text-secondary);
  margin-bottom: var(--space-md);
}

.body-small {
  font-size: var(--font-size-sm);
  line-height: var(--line-height-normal);
  color: var(--text-muted);
  margin-bottom: var(--space-sm);
}

.body-xs {
  font-size: var(--font-size-xs);
  line-height: var(--line-height-normal);
  color: var(--text-muted);
  margin-bottom: var(--space-xs);
}

.lead {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-medium);
  line-height: var(--line-height-relaxed);
  color: var(--text-primary);
  margin-bottom: var(--space-lg);
}

.lead-large {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-medium);
  color: var(--text-primary);
  margin-bottom: var(--space-xl);
  line-height: 1.4;
}

.code {
  font-family: JetBrains Mono, Courier New, monospace;
  font-size: var(--font-size-sm);
  background: var(--bg-tertiary);
  color: var(--text-primary);
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-sm);
  border: 1px solid var(--border-primary);
}

.code-block {
  font-family: JetBrains Mono, Courier New, monospace;
  font-size: var(--font-size-sm);
  background: var(--bg-tertiary);
  color: var(--text-primary);
  padding: var(--space-lg);
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-primary);
  margin: var(--space-lg) 0;
  overflow-x: auto;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-justify {
  text-align: justify;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-lowercase {
  text-transform: lowercase;
}

.text-capitalize {
  text-transform: capitalize;
}

.text-decoration-none {
  text-decoration: none;
}

.text-decoration-underline {
  text-decoration: underline;
}

.text-decoration-line-through {
  text-decoration: line-through;
}

.font-light {
  font-weight: 300;
}

.font-normal {
  font-weight: 400;
}

.font-medium {
  font-weight: 500;
}

.font-semibold {
  font-weight: 600;
}

.font-bold {
  font-weight: 700;
}

.font-extrabold {
  font-weight: 800;
}

.font-black {
  font-weight: 900;
}

.font-normal {
  font-style: normal;
}

.font-italic {
  font-style: italic;
}

.leading-tight {
  line-height: 1.25;
}

.leading-normal {
  line-height: 1.5;
}

.leading-relaxed {
  line-height: 1.75;
}

.leading-loose {
  line-height: 2;
}

.tracking-tighter {
  letter-spacing: -.05em;
}

.tracking-tight {
  letter-spacing: -.025em;
}

.tracking-normal {
  letter-spacing: 0;
}

.tracking-wide {
  letter-spacing: .025em;
}

.tracking-wider {
  letter-spacing: .05em;
}

.tracking-widest {
  letter-spacing: .1em;
}

.text-primary {
  color: var(--text-primary);
}

.text-secondary {
  color: var(--text-secondary);
}

.text-muted {
  color: var(--text-muted);
}

.text-inverse {
  color: var(--text-inverse);
}

.text-success {
  color: var(--success);
}

.text-warning {
  color: var(--warning);
}

.text-danger {
  color: var(--danger);
}

.text-info {
  color: var(--info);
}

.text-xs {
  font-size: var(--font-size-xs);
}

.text-sm {
  font-size: var(--font-size-sm);
}

.text-base {
  font-size: var(--font-size-base);
}

.text-lg {
  font-size: var(--font-size-lg);
}

.text-xl {
  font-size: var(--font-size-xl);
}

.text-2xl {
  font-size: var(--font-size-2xl);
}

.text-3xl {
  font-size: var(--font-size-3xl);
}

.text-4xl {
  font-size: var(--font-size-4xl);
}

@media (width <= 768px) {
  .text-xs-mobile {
    font-size: var(--font-size-xs);
  }

  .text-sm-mobile {
    font-size: var(--font-size-sm);
  }

  .text-base-mobile {
    font-size: var(--font-size-base);
  }

  .text-lg-mobile {
    font-size: var(--font-size-lg);
  }

  .text-xl-mobile {
    font-size: var(--font-size-xl);
  }

  .text-2xl-mobile {
    font-size: var(--font-size-2xl);
  }

  .text-3xl-mobile {
    font-size: var(--font-size-3xl);
  }

  .text-4xl-mobile {
    font-size: var(--font-size-4xl);
  }
}

@media (width >= 769px) and (width <= 1024px) {
  .text-xs-tablet {
    font-size: var(--font-size-xs);
  }

  .text-sm-tablet {
    font-size: var(--font-size-sm);
  }

  .text-base-tablet {
    font-size: var(--font-size-base);
  }

  .text-lg-tablet {
    font-size: var(--font-size-lg);
  }

  .text-xl-tablet {
    font-size: var(--font-size-xl);
  }

  .text-2xl-tablet {
    font-size: var(--font-size-2xl);
  }

  .text-3xl-tablet {
    font-size: var(--font-size-3xl);
  }

  .text-4xl-tablet {
    font-size: var(--font-size-4xl);
  }
}

@media (width >= 1025px) {
  .text-xs-desktop {
    font-size: var(--font-size-xs);
  }

  .text-sm-desktop {
    font-size: var(--font-size-sm);
  }

  .text-base-desktop {
    font-size: var(--font-size-base);
  }

  .text-lg-desktop {
    font-size: var(--font-size-lg);
  }

  .text-xl-desktop {
    font-size: var(--font-size-xl);
  }

  .text-2xl-desktop {
    font-size: var(--font-size-2xl);
  }

  .text-3xl-desktop {
    font-size: var(--font-size-3xl);
  }

  .text-4xl-desktop {
    font-size: var(--font-size-4xl);
  }
}

.quote {
  font-size: var(--font-size-lg);
  font-style: italic;
  line-height: var(--line-height-relaxed);
  color: var(--text-secondary);
  border-left: 4px solid var(--primary);
  padding-left: var(--space-lg);
  margin: var(--space-xl) 0;
  position: relative;
}

.quote:before {
  content: "\"";
  color: var(--primary);
  opacity: .3;
  font-size: 4rem;
  position: absolute;
  top: -1rem;
  left: -1rem;
}

.highlight {
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-sm);
  font-weight: var(--font-weight-medium);
  background: linear-gradient(120deg, #6366f11a 0%, #06b6d41a 100%);
}

.truncate {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.truncate-2 {
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
}

.truncate-3 {
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
}

.typewriter {
  border-right: 2px solid var(--primary);
  white-space: nowrap;
  animation: 3.5s steps(40, end) typing, .75s step-end infinite blink-caret;
  overflow: hidden;
}

@keyframes typing {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

@keyframes blink-caret {
  from, to {
    border-color: #0000;
  }

  50% {
    border-color: var(--primary);
  }
}

.text-fade-in {
  opacity: 0;
  animation: .6s ease-out forwards textFadeIn;
}

@keyframes textFadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.text-slide-in-left {
  opacity: 0;
  animation: .6s ease-out forwards textSlideInLeft;
  transform: translateX(-50px);
}

@keyframes textSlideInLeft {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.text-slide-in-right {
  opacity: 0;
  animation: .6s ease-out forwards textSlideInRight;
  transform: translateX(50px);
}

@keyframes textSlideInRight {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.text-sm, .text-xs {
  font-weight: var(--font-weight-medium);
}

ul, ol {
  line-height: var(--line-height-relaxed);
  margin-bottom: var(--space-md);
}

li {
  margin-bottom: var(--space-xs);
}

a {
  text-underline-offset: 2px;
  text-decoration-thickness: 2px;
}

p {
  max-width: 65ch;
  margin-bottom: var(--space-md);
}

@media (prefers-reduced-motion: reduce) {
  .typewriter, .text-fade-in, .text-slide-in-left, .text-slide-in-right {
    animation: none;
  }
}

@media (prefers-color-scheme: dark) {
  .text-primary {
    color: #f8fafc;
  }

  .text-secondary {
    color: #cbd5e1;
  }

  .text-muted {
    color: #94a3b8;
  }

  .code, .code-block {
    color: #e2e8f0;
    background: #1e293b;
    border-color: #334155;
  }
}

@media (prefers-contrast: high) {
  .text-secondary, .text-muted {
    color: var(--text-primary);
  }

  .code, .code-block {
    border-width: 2px;
  }
}

.card-modern {
  backdrop-filter: blur(20px);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  padding: var(--space-xl);
  transition: var(--transition-normal);
  background: #fffffff2;
  border: 1px solid #fff3;
  position: relative;
  overflow: hidden;
}

.card-modern:before {
  content: "";
  background: linear-gradient(90deg, var(--primary), var(--accent), var(--info));
  background-size: 200% 100%;
  height: 4px;
  animation: 3s infinite gradientShift;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.card-modern:hover {
  box-shadow: var(--shadow-2xl);
  transform: translateY(-8px);
}

.card-modern .card-header {
  margin-bottom: var(--space-lg);
  padding-bottom: var(--space-md);
  border-bottom: 1px solid var(--border-secondary);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card-modern .card-title {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  margin: 0;
}

.card-modern .card-subtitle {
  font-size: var(--font-size-sm);
  color: var(--text-secondary);
  margin-top: var(--space-xs);
}

.badge-modern {
  align-items: center;
  gap: var(--space-xs);
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-full);
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-semibold);
  text-transform: uppercase;
  letter-spacing: .05em;
  box-shadow: var(--shadow-sm);
  transition: var(--transition-normal);
  display: inline-flex;
  position: relative;
  overflow: hidden;
}

.badge-modern:before {
  content: "";
  width: 100%;
  height: 100%;
  transition: var(--transition-normal);
  background: linear-gradient(90deg, #0000, #ffffff4d, #0000);
  position: absolute;
  top: 0;
  left: -100%;
}

.badge-modern:hover:before {
  left: 100%;
}

.badge-modern:hover {
  box-shadow: var(--shadow-md);
  transform: translateY(-1px);
}

.badge-primary {
  background: linear-gradient(135deg, var(--primary), var(--primary-light));
  color: var(--text-inverse);
}

.badge-success {
  background: linear-gradient(135deg, var(--success), var(--success-light));
  color: var(--text-inverse);
}

.badge-warning {
  background: linear-gradient(135deg, var(--warning), var(--warning-light));
  color: var(--text-inverse);
}

.badge-danger {
  background: linear-gradient(135deg, var(--danger), var(--danger-light));
  color: var(--text-inverse);
}

.badge-info {
  background: linear-gradient(135deg, var(--info), var(--info-light));
  color: var(--text-inverse);
}

.avatar {
  border-radius: var(--radius-full);
  background: linear-gradient(135deg, var(--primary), var(--accent));
  width: 48px;
  height: 48px;
  color: var(--text-inverse);
  font-weight: var(--font-weight-bold);
  font-size: var(--font-size-lg);
  box-shadow: var(--shadow-md);
  transition: var(--transition-normal);
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.avatar:before {
  content: "";
  transition: var(--transition-normal);
  background: linear-gradient(45deg, #0000 30%, #fff3 50%, #0000 70%);
  position: absolute;
  inset: 0;
  transform: translateX(-100%);
}

.avatar:hover:before {
  transform: translateX(100%);
}

.avatar:hover {
  box-shadow: var(--shadow-lg);
  transform: scale(1.1);
}

.avatar-sm {
  width: 32px;
  height: 32px;
  font-size: var(--font-size-sm);
}

.avatar-lg {
  width: 64px;
  height: 64px;
  font-size: var(--font-size-xl);
}

.avatar-xl {
  width: 96px;
  height: 96px;
  font-size: var(--font-size-2xl);
}

.progress-modern {
  background: var(--bg-tertiary);
  border-radius: var(--radius-full);
  width: 100%;
  height: 8px;
  position: relative;
  overflow: hidden;
  box-shadow: inset 0 1px 3px #0000001a;
}

.progress-modern:before {
  content: "";
  background: linear-gradient(90deg, var(--primary), var(--accent));
  border-radius: var(--radius-full);
  height: 100%;
  transition: width .6s;
  animation: 2s ease-in-out infinite progressShine;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes progressShine {
  0% {
    background-position: -100px;
  }

  100% {
    background-position: 200px;
  }
}

.progress-modern[data-progress="25"]:before {
  width: 25%;
}

.progress-modern[data-progress="50"]:before {
  width: 50%;
}

.progress-modern[data-progress="75"]:before {
  width: 75%;
}

.progress-modern[data-progress="100"]:before {
  width: 100%;
}

.tooltip-modern {
  cursor: help;
  display: inline-block;
  position: relative;
}

.tooltip-modern:before {
  content: attr(data-tooltip);
  background: var(--text-primary);
  color: var(--text-inverse);
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-md);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  white-space: nowrap;
  opacity: 0;
  visibility: hidden;
  transition: var(--transition-normal);
  z-index: 1000;
  box-shadow: var(--shadow-lg);
  position: absolute;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
}

.tooltip-modern:after {
  content: "";
  border: 5px solid #0000;
  border-top-color: var(--text-primary);
  opacity: 0;
  visibility: hidden;
  transition: var(--transition-normal);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.tooltip-modern:hover:before, .tooltip-modern:hover:after {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%)translateY(-5px);
}

.modal-modern {
  backdrop-filter: blur(5px);
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  transition: var(--transition-normal);
  background: #00000080;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-modern.show {
  opacity: 1;
  visibility: visible;
}

.modal-content {
  background: var(--bg-primary);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-2xl);
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
  transition: var(--transition-normal);
  position: relative;
  overflow-y: auto;
  transform: scale(.9);
}

.modal-modern.show .modal-content {
  transform: scale(1);
}

.modal-header {
  padding: var(--space-xl);
  border-bottom: 1px solid var(--border-secondary);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.modal-title {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-semibold);
  color: var(--text-primary);
  margin: 0;
}

.modal-close {
  font-size: var(--font-size-xl);
  color: var(--text-secondary);
  cursor: pointer;
  padding: var(--space-sm);
  border-radius: var(--radius-md);
  transition: var(--transition-normal);
  background: none;
  border: none;
}

.modal-close:hover {
  background: var(--bg-secondary);
  color: var(--text-primary);
}

.modal-body {
  padding: var(--space-xl);
}

.modal-footer {
  padding: var(--space-xl);
  border-top: 1px solid var(--border-secondary);
  gap: var(--space-md);
  justify-content: flex-end;
  display: flex;
}

.dropdown-modern {
  display: inline-block;
  position: relative;
}

.dropdown-menu {
  background: var(--bg-primary);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-xl);
  border: 1px solid var(--border-primary);
  opacity: 0;
  visibility: hidden;
  min-width: 200px;
  transition: var(--transition-normal);
  z-index: 100;
  position: absolute;
  top: 100%;
  left: 0;
  overflow: hidden;
  transform: translateY(-10px);
}

